'use client';

import { useState, useRef, useEffect } from 'react';
import { Palette, Moon, Sun, Zap } from 'lucide-react';
import ChatbotHeader from '@/components/ChatbotHeader';
import ChatMessage from '@/components/ChatMessage';
import ChatInput from '@/components/ChatInput';
import ThemeSelector from '@/components/ThemeSelector';

type Message = {
  id: string;
  content: string;
  role: 'user' | 'assistant';
  timestamp: Date;
};

type Theme = 'light' | 'dark' | 'gradient' | 'minimal';

const initialMessages: Message[] = [
  {
    id: '1',
    content: 'Hello! I\'m your AI assistant. How can I help you today?',
    role: 'assistant',
    timestamp: new Date(Date.now() - 3600000),
  },
  {
    id: '2',
    content: 'Can you help me with a coding problem?',
    role: 'user',
    timestamp: new Date(Date.now() - 1800000),
  },
  {
    id: '3',
    content: 'Sure! I\'d be happy to help with coding problems. What language are you working with and what challenge are you facing?',
    role: 'assistant',
    timestamp: new Date(),
  },
];

const themeConfigs = {
  light: {
    bg: 'bg-gradient-to-br from-gray-50 to-gray-100',
    header: 'bg-white/80 border-b',
    container: 'bg-white',
    text: 'text-gray-900',
    secondaryText: 'text-gray-600',
  },
  dark: {
    bg: 'bg-gradient-to-br from-gray-900 to-gray-800',
    header: 'bg-gray-800/80 border-gray-700',
    container: 'bg-gray-800',
    text: 'text-gray-100',
    secondaryText: 'text-gray-400',
  },
  gradient: {
    bg: 'bg-gradient-to-br from-indigo-50 via-purple-50 to-pink-50',
    header: 'bg-white/70 backdrop-blur-md border-indigo-100',
    container: 'bg-gradient-to-r from-white to-indigo-50/50',
    text: 'text-gray-900',
    secondaryText: 'text-indigo-600',
  },
  minimal: {
    bg: 'bg-white',
    header: 'bg-white border-b',
    container: 'bg-gray-50',
    text: 'text-gray-900',
    secondaryText: 'text-gray-500',
  },
};

export default function ChatbotPage() {
  const [messages, setMessages] = useState<Message[]>(initialMessages);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [copiedMessageId, setCopiedMessageId] = useState<string | null>(null);
  const [currentTheme, setCurrentTheme] = useState<Theme>('light');
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSendMessage = async () => {
    if (!input.trim() || isLoading) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      content: input,
      role: 'user',
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);

    try {
      // Simulate API call with different responses based on theme
      await new Promise(resolve => setTimeout(resolve, 800));

      let response = '';
      switch(currentTheme) {
        case 'dark':
          response = `ðŸŒ™ **Dark Theme Response**: "${input}"\n\nIn dark mode, your eyes are protected and the interface is sleek. What else would you like to know?`;
          break;
        case 'gradient':
          response = `ðŸŒˆ **Gradient Theme Response**: "${input}"\n\nThe gradient theme brings vibrant colors to our conversation! How can I assist you further?`;
          break;
        case 'minimal':
          response = `âšª **Minimal Theme Response**: "${input}"\n\nClean and simple - just the essentials. What's your next question?`;
          break;
        default:
          response = `â˜€ï¸ **Light Theme Response**: "${input}"\n\nBright and clear communication! How else can I help you today?`;
      }

      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        content: response,
        role: 'assistant',
        timestamp: new Date(),
      };

      setMessages(prev => [...prev, assistantMessage]);
    } catch (error) {
      console.error('Error sending message:', error);
      
      const errorMessage: Message = {
        id: (Date.now() + 2).toString(),
        content: 'Sorry, I encountered an error. Please try again.',
        role: 'assistant',
        timestamp: new Date(),
      };
      
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleClearChat = () => {
    setMessages([
      {
        id: '1',
        content: `Chat cleared! I'm ready to help you in ${currentTheme} theme.`,
        role: 'assistant',
        timestamp: new Date(),
      },
    ]);
  };

  const handleCopyMessage = (content: string, messageId: string) => {
    navigator.clipboard.writeText(content);
    setCopiedMessageId(messageId);
    setTimeout(() => setCopiedMessageId(null), 2000);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const theme = themeConfigs[currentTheme];

  return (
    <div className={`flex flex-col h-screen transition-all duration-300 ${theme.bg}`}>
      <ChatbotHeader 
        onClearChat={handleClearChat} 
        currentTheme={currentTheme}
      />
      
      <ThemeSelector 
        currentTheme={currentTheme}
        onThemeChange={setCurrentTheme}
      />
      
      <div className="flex-1 overflow-hidden flex flex-col max-w-4xl mx-auto w-full p-4 md:p-6">
        {/* Messages container */}
        <div className="flex-1 overflow-y-auto mb-4 space-y-6 p-2 md:p-4">
          {messages.map((message) => (
            <ChatMessage
              key={message.id}
              message={message}
              isCopied={copiedMessageId === message.id}
              onCopy={() => handleCopyMessage(message.content, message.id)}
              theme={currentTheme}
            />
          ))}
          
          {isLoading && (
            <div className="flex items-center space-x-2">
              <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                currentTheme === 'dark' 
                  ? 'bg-gray-700' 
                  : currentTheme === 'gradient'
                  ? 'bg-gradient-to-r from-indigo-100 to-purple-100'
                  : 'bg-gray-100'
              }`}>
                <div className={`w-5 h-5 rounded-full animate-spin border-2 ${
                  currentTheme === 'dark'
                    ? 'border-gray-600 border-t-gray-400'
                    : currentTheme === 'gradient'
                    ? 'border-indigo-200 border-t-indigo-500'
                    : 'border-gray-200 border-t-gray-600'
                }`} />
              </div>
              <span className={`text-sm ${theme.secondaryText}`}>
                AI is thinking...
              </span>
            </div>
          )}
          
          <div ref={messagesEndRef} />
        </div>

        {/* Input area */}
        <ChatInput
          input={input}
          setInput={setInput}
          isLoading={isLoading}
          onSend={handleSendMessage}
          onKeyPress={handleKeyPress}
          theme={currentTheme}
        />
      </div>
      
      <footer className="text-center py-4">
        <p className={`text-sm ${theme.secondaryText}`}>
          Current theme: <span className="font-medium capitalize">{currentTheme}</span>
        </p>
      </footer>
    </div>
  );
}